pe=\"text\" v-model=\"editValue[lang]\" required style=\"width: calc(100% - 100px)\">\n        </div>\n\n        <div class=\"pull-right\">\n          <NcActions>\n            <NcActionButton @click=\"removeEdit\" icon=\"icon-delete\"></NcActionButton>\n          </NcActions>\n        </div>\n\n        <NcActions>\n          <NcActionButton @click=\"saveEdit\" icon=\"icon-checkmark\"></NcActionButton>\n        </NcActions>\n      </div>\n    </NcModal>\n  </div>\n</template>\n\n<style>\n.modal__content {\n  padding: 10px;\n}\n\n.modal__content .lang {\n  width: 60px;\n  display: inline-block;\n  padding: 4px;\n  box-sizing: border-box;\n}\n\n.pull-right {\n  float: right;\n}\n</style>\n\n<style scoped>\n.hide {\n  display: none;\n}\n</style>\n\n<script>\nimport NcModal from '@nextcloud/vue/dist/Components/NcModal'\nimport NcActions from '@nextcloud/vue/dist/Components/NcActions'\nimport NcActionButton from '@nextcloud/vue/dist/Components/NcActionButton'\n\nexport default {\n  name: 'AdminCategoriesCustom',\n  components: {\n    NcModal,\n    NcActions,\n    NcActionButton,\n  },\n  data() {\n    return {\n      input: null,\n      values: [],\n      langs: [],\n      addForm: false,\n      editForm: false,\n      newValue: {},\n      editValue: {},\n    }\n  },\n  methods: {\n    init() {\n      this.values = JSON.parse(this.input.value)\n      this.langs = JSON.parse(this.input.getAttribute('data-langs'))\n    },\n    update() {\n      this.input.value = JSON.stringify(this.values)\n    },\n    showAddForm() {\n      this.newValue = {id: 'cat' + Math.random().toString().replace('0.', '')}\n\n      this.addForm = true\n    },\n    showEditForm(value) {\n      this.editValue = {id: value.id}\n\n      for (let i of this.langs) {\n        this.editValue[i] = typeof value[i] !== 'undefined' ? value[i] : ''\n      }\n\n      this.editForm = true\n    },\n    saveAdd() {\n      for (let i of this.langs) {\n        if (!this.newValue[i] || /^\\s*$/.test(this.newValue[i])) {\n          return\n        }\n      }\n\n      this.values.push(this.newValue)\n      this.update()\n      this.hideAddForm()\n      this.newValue = {}\n    },\n    saveEdit() {\n      for (let i of this.langs) {\n        if (!this.editValue[i] || /^\\s*$/.test(this.editValue[i])) {\n          return\n        }\n      }\n\n      for (let i in this.values) {\n        if (this.values[i].id === this.editValue.id) {\n          this.values[i] = this.editValue\n        }\n      }\n\n      this.update()\n      this.hideEditForm()\n    },\n    removeEdit() {\n      for (let i in this.values) {\n        if (this.values[i].id === this.editValue.id) {\n          this.values.splice(i, 1);\n        }\n      }\n\n      this.update()\n      this.hideEditForm()\n    },\n    hideAddForm() {\n      this.addForm = false\n    },\n    hideEditForm() {\n      this.editForm = false\n    },\n  },\n  mounted() {\n    this.input = document.querySelector('input[name=\"categories-custom\"]')\n    this.init()\n  }\n}\n</script>\n"],"sourceRoot":""}]);
